var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno',{attrs:{"subTitulo":'4. Historia del castigo en la infancia'}}),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5 overflow-hidden"},[_vm._m(0),_vm._m(1),_vm._m(2),_c('div',{staticClass:"row align-items-start mb-5"},[_vm._m(3),_c('div',{staticClass:"col-lg-8"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Antigua Grecia y Roma"}},[_c('p',[_vm._v("El castigo físico fue visto como una herramienta esencial para \"disciplinar\" a los niños. En esas culturas, la autoridad de los adultos se ejercía de forma rígida, y la idea de que los niños debían ser “domados” para encajar en una sociedad ordenada era predominante. En esos tiempos, la relación entre padres e hijos se basaba en un modelo jerárquico, donde se esperaba obediencia absoluta, sin cuestionar el uso del castigo como medio para lograrlo. Esta práctica estaba profundamente enraizada en la visión de que el dolor era necesario para enseñar el concepto de bien y el mal (López, 2016).")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Edad Media"}},[_c('p',[_vm._v("A lo largo de la Edad Media, la religión jugó un papel fundamental en cómo se concebía la educación infantil. El castigo físico no solo se utilizaba para corregir comportamientos, sino también como un mecanismo moral, asociado a la idea de purificar el alma del niño (Londoño, Álzate y Rueda, 2016). La disciplina era vista como una forma de salvar al niño de \"pecados\" o comportamientos inmorales, y aunque esta visión pudo haber ofrecido consuelo en su tiempo, no tomaba en cuenta los efectos emocionales o psicológicos que este tipo de corrección pudiera causar.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Renacimiento y el comienzo de la era moderna"}},[_c('p',[_vm._v("En los siglos posteriores, durante el Renacimiento y el comienzo 