as que, por generaciones, han legitimado la violencia como forma de educación.")]),_c('div',{staticClass:"bg-full-width bg-color-6 mb-5"},[_c('div',{staticClass:"px-4 p-md-5"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-7 mb-3 mb-lg-0"},[_c('h2',{staticClass:"mb-4",attrs:{"data-aos":"flip-up"}},[_vm._v("Naturalización del castigo en la crianza")]),_vm._m(7),_c('a',{staticClass:"anexo mb-4 bg-white w-fit",attrs:{"href":_vm.obtenerLink('/downloads/Anexo_1.pdf'),"target":"_blank","data-aos":"flip-up"}},[_c('div',{staticClass:"anexo__icono",style:({'background-color': '#FCDFDB'})},[_c('img',{attrs:{"src":require("@/assets/template/icono-pdf.svg")}})]),_vm._m(8)])]),_vm._m(9)])])]),_vm._m(10),_vm._m(11),_c('div',{staticClass:"bg-full-width bg-fondo-slider mb-5"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('SlyderA',{staticClass:"bg-white",attrs:{"tipo":"b"}},[_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 order-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema4/10.png")}})])]),_c('div',{staticClass:"col-lg-7 order-1 mb-3 mb-lg-0"},[_c('p',[_vm._v("Durante generaciones, se ha transmitido la idea de que el castigo. ya sea físico o emocional, es una forma válida de corregir comportamientos, enseñar límites o \"preparar para la vida\". Muchas veces, se actúa desde la creencia de que \"es por su bien\", sin detenerse a pensar en las consecuencias profundas que estas prácticas pueden tener en la autoestima, la confianza y el desarrollo socioemocional de los niños y las niñas (Lassi, 2008).")])])]),_c('div',{staticClass:"row align-items-center p-4 p-md-5"},[_c('div',{staticClass:"col-lg-5 order-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema4/11.png")}})])]),_c('div',{staticClass:"col-lg-7 order-1 mb-3 mb-lg-0"},[_c('p',[_vm._v("Además, en contextos donde existen altos niveles de estrés, carencias económicas, falta de redes de apoyo o es